import React from 'react'
import './css/firstSection.css'
import Typed from 'typed.js'
import robot_img from '../images/Robot-PNG-File.png'

import { Helmet } from 'react-helmet-async';

export default function FirstSection() {
  const typing_animation = React.useRef(null);

  React.useEffect(() => {
    const typed = new Typed(typing_animation.current, {
      strings: ['Samarth', 'Programmer', 'Samarth'],
      typeSpeed: 100,
    });

    return () => {
      // Destroy Typed instance during cleanup to stop animation
      typed.destroy();
    };
  }, []);



  const subscribed = () => {
    alert('Sorry! This website is currently on develpment, soon it will be ready.')
  }
  return (


    <div className='FirstSection'>

      <Helmet>
        <title>Code With SAM</title>
      </Helmet>


      <div className='FirstSection-MyName'>
        Hey, I am <span ref={typing_animation} className='text-blue'>Samarth!</span>
      </div>


      <div className="FirstSection-about">Hello! I am samarth and this is my blog page, I am a programmer who learnt programming from youtube.</div>


      <div className="FirstSection-EmailField-element">
        <input type="text" placeholder='email' className='FirstSection-email-input-field' />
        <button onClick={() => { subscribed() }}>Subscribe</button>
      </div>


      <div className="FirstSection-img">
        <img src={robot_img} alt="CodeWithSam" className="landing-robot-img" />
      </div>


    </div>
  )
}
