import React, { useRef } from 'react'
import './css/navbar.css'
import { NavLink, Link } from 'react-router-dom';

export default function Navbar() {
    const menu_btn = useRef(null);
    const resp_navbar = useRef(null);



    const Open_menu = () => {
        if (menu_btn.current.getAttribute('name') === 'menu') {
            // open navbar
            resp_navbar.current.style = 'transform: translateX(0%);';
            menu_btn.current.setAttribute('name', 'close');

        }

        else {
            // close navbar
            resp_navbar.current.style = 'transform: translateX(100%);'
            menu_btn.current.setAttribute('name', 'menu');
        }
    }

    return (
        <>
            <div className='Navbar'>
                <div className="logo " ><Link to={'/'} className="text-white">CodeWithSam</Link></div>
                <div className="centerNavbar">
                    <ul>
                        <li><NavLink to={'/'} className='text-white'>HOME</NavLink></li>
                        <li><NavLink to={'/all-posts'} className='text-white'>POSTS</NavLink></li>
                        <li><NavLink to={'/contact-us'} className='text-white' >CONTACT</NavLink></li>
                    </ul>
                </div>
                 <div className="rightNabar"> <Link to={'/all-posts'}><ion-icon name="search"></ion-icon></Link></div>


                <ion-icon name="menu" id="menu-btn" ref={menu_btn} onClick={() => { Open_menu() }} ></ion-icon>
                <div className="responsivee-centerNavbar" ref={resp_navbar}>
                    <ul>
                        <li><NavLink to={'/'} className='text-white'>HOME</NavLink></li>
                        <li><NavLink to={'/all-posts'} className='text-white'>POSTS</NavLink></li>
                        <li><NavLink to={'/contact-us'} className='text-white' >CONTACT</NavLink></li>
                    </ul>
                </div>
            </div>



        </>
    )
}
