import React from 'react'
import './css/SinglePosts.css'
import { useRef, useEffect } from 'react';
import { NavLink, Link } from 'react-router-dom';
import { Helmet } from 'react-helmet-async';

import createRepo from './ReactRouterGithubImages/MakeRepo1.png'
import Hashroutercode from './ReactRouterGithubImages/CodeForhashrouter2.png'
import Thumbnail from './ReactRouterGithubImages/MainThumbnail.png'

export default function ReactRouterGithubPost() {
  const menu_btn = useRef(null);
  const resp_navbar = useRef(null);



  const Open_menu = () => {
    if (menu_btn.current.getAttribute('name') === 'menu') {
      // open navbar
      resp_navbar.current.style = 'transform: translateX(0%);';
      menu_btn.current.setAttribute('name', 'close');

    }

    else {
      // close navbar
      resp_navbar.current.style = 'transform: translateX(100%);'
      menu_btn.current.setAttribute('name', 'menu');
    }
  }


  const single_posts = useRef(null);
  const cws_logo = useRef(null);
  const search_btn = useRef(null);
  const centerNavbar = useRef(null);
  const resp_centerNavbar = useRef(null);

  useEffect(() => {
    if (single_posts.current.getAttribute('class') === 'Avb-Posts') {
      var styleText = 'color:black;';
      cws_logo.current.style = styleText;
      search_btn.current.style = 'color: black;';
      centerNavbar.current.style = 'color: black;';
      menu_btn.current.style = 'color: black;';
      resp_centerNavbar.current.style = 'background: transparent;';
    }

  }, [])

  return (
    <div className='Avb-Posts' ref={single_posts}>
      <Helmet>
        <title>How to deploy react js website with routing on Github || Code With SAM</title>
      </Helmet>


      <div className="Avb-Posts-navbar">

        <div className='Navbar'>
          <div className="logo " ref={cws_logo}><Link to={'/'} className="text-black">CodeWithSam</Link></div>
          <div className="centerNavbar" >
            <ul ref={centerNavbar}>
              <li><NavLink to={'/'} className="text-black">HOME</NavLink> </li>
              <li><NavLink to={'/all-posts'} className="text-black">POSTS</NavLink></li>
              <li><NavLink to={'/contact-us'} className="text-black">CONTACT</NavLink></li>
            </ul>
          </div>
          <div className="rightNabar" ref={resp_centerNavbar}><Link to={'/all-posts'} className="text-black"><ion-icon name="search" ref={search_btn}></ion-icon></Link></div>


          <ion-icon name="menu" id="menu-btn" ref={menu_btn} onClick={() => { Open_menu() }} ></ion-icon>


          <div className="responsive-centerNavbar" ref={resp_navbar}>
            <ul>
              <li><NavLink to={'/'} className="text-black">HOME</NavLink> </li>
              <li><NavLink to={'/all-posts'} className="text-black">POSTS</NavLink></li>
              <li><NavLink to={'/contact-us'} className="text-black">CONTACT</NavLink></li>
            </ul>
          </div>
        </div>

      </div>


      <hr />


      <div className="Avb-Posts-mainHeading">
        How to deploy React website with Router on Github Pages?
      </div>


      <div className="Avb-Posts-landing-img">
        <img src={Thumbnail} alt="CodeWithSam" />
      </div>

      <div className="Avb-Posts-paragraph">
        Hey! guys. How are you? In this blog I will teach you how to deploy react website with <b>Router</b> on github pages. Deploying normal website which just contains HTML, CSS and Javascript on Github is quite easier, but deploying React page on github that contains React Router is much harder than normal Deployment. After Reading this blog I can assure that you will never search on this topic next time!!
        So, let's learn <b>How to deploy React Router website on Github pages</b>
      </div>

      <div className="Avb-Posts-paragraph no-first-captial">
        <b>Step 1:</b> First make a react website by installing react packages with yarn or npm. Edit the src folder as your choice.
      </div>

      <div className="Avb-Posts-paragraph no-first-captial">
        <b>Step 2:</b> Now as we need React router website, install the router package by pasting given command in terminal.
        We will install version 6 of react router dom package as it is easy to use
      </div>


      <div className="code">
        <code>
          npm install react-router-dom@6.0.0
        </code>
      </div>



      <div className="Avb-Posts-paragraph no-first-captial">
        <b>Step 3:</b> Many people use &lt;BrowseRouter&gt; in App.js but this will not work in github so we should add &lt;HashRouter&gt; tag as given.
        In path you should give the path of particular page, and in element you should write particular component that you want to render in that path.
      </div>



      <div className="Avb-middle-images">
        <img src={Hashroutercode} alt="CodeWithSam" style={{ width: '70%', borderRadius: '20px' }} />
      </div>

      <div className="Avb-Posts-paragraph no-first-captial">
        Now your wesite is ready in development purpose, let's deploy it!
      </div>

      <div className="Avb-Posts-paragraph no-first-captial">
        <b>Step 4:</b>  Create a Public Github repo, Name it anything!!
      </div>

      <div className="Avb-middle-images">
        <img src={createRepo} alt="CodeWithSam" />
      </div>


      <div className="Avb-Posts-paragraph no-first-captial">
        <b>Step 5:</b> Add link of Repository as a remote to your project. For that you need to type command:
      </div>

      <div className="code">
        <code>
          git remote add &lt;name-of-remote&gt; &lt;url-of-repository&gt;
        </code>
      </div>

      <div className="Avb-Posts-paragraph no-first-captial">
        In my case it is:
      </div>

      <div className="code">
        <code>
          git remote add origin https://github.com/SamarthAkulkarni/My-page
        </code>
      </div>

      <div className="Avb-Posts-paragraph no-first-captial">
        <b>Step 6:</b> Now we should set remote as origin. To do so paste the following cmd in terminal
      </div>

      <div className="code">
        <code>
          git push --set-upstream origin master
        </code>
      </div>


      <div className="Avb-Posts-paragraph no-first-captial">
        <b>Step 7:</b> Next, We need to install a package that will help us to deploy this website on github pages. Paste the following command in terminal.
      </div>


      <div className="code">
        <code>
          npm install gh-pages
        </code>
      </div>


      <div className="Avb-Posts-paragraph no-first-captial">
        <b>Step 8:</b> To Use gh-pages we need to edit package.json. Just add <b>homepage</b> in top of package.json and deploy script in scripts value i.e <b>predeploy</b> and <b>deploy</b> keys.
      </div>


      <div className="code">
        <code>
          &#123; <br />
          "name": "My-page", <br />
          "version" : "0.1.0", <br />
          "homepage": "https://samarthAkulkarni.github.io/My-page", <br />
          /... <br />
          &#125;
        </code>
      </div>

      <div className="code" style={{ margin: "50px auto", }}>
        <code>
          "scripts": &#123; <br />
          <br />
          "start": "react-scripts start", <br />
          "predeploy": "npm run build", <br />
          "deploy": "gh-pages -d build", <br />
          "build": "react-scripts build", <br />
          "test": "react-scripts test", <br />
          "eject": "react-scripts eject" <br />
          <br />
          &#125;,
        </code>
      </div>



      <div className="Avb-Posts-paragraph no-first-captial">
        <b>Step 9:</b> Finally we are Ready to deploy Our website on Github !! To do so you just need to paste the given command and Bang the enter button on your keyboard!
      </div>


      <div className="code">
        <code>
          npm run deploy
        </code>
      </div>



      <div className="Avb-Posts-paragraph no-first-captial">
        Now, Just vist the url we pasted in package.json of homepage key and boom! Your website is successfully hosted on Github Pages <br />
        If You think this blog was helpful, Please share this and I think I have cleared all Your doubts <br />
        <b>Happy Coding!!!</b>
      </div>
      <hr />





      <div className="Avb-Posts-popularposts">

        <Link to={'/how-to-deploy-react-router-website-on-github'}>
          <div className="allposts-post1 allposts">
            <div className="allposts-img">
              <img src={Thumbnail} alt="CodeWithSam" />
            </div>
            <div className="allposts-title">
              How to deploy React website on Github.
            </div>
            <div className="allposts-description">In this blog I will teach you how to deploy react website with <b>Router</b> on github pages. I will make sure that this blog is more than enough to make you understand how the github pages deployments work with React website that contains Router</div>

            <div className="allposts-upload-date">22-07-2023</div>
          </div>
        </Link>






      </div>
    </div>
  )
}
